onospace);\n  transition: border-color 0.2s ease, background 0.2s ease;\n  resize: none;\n  min-height: 96px;\n  max-height: 520px;\n  overflow-y: auto;\n  line-height: 1.5;\n  box-sizing: border-box;\n  width: 100%;\n  display: block;\n\n  &:focus {\n    outline: none;\n    border-color: $em-color;\n    background: $bg-hover;\n  }\n\n  &::placeholder {\n    color: $text-muted;\n    opacity: 0.7;\n  }\n\n  @media (max-width: $mobile-breakpoint) {\n    padding: 10px 12px;\n    font-size: 14px;\n    max-height: 420px;\n  }\n}\n\n.toggle-checkbox {\n  flex: 1;\n  min-width: 0;\n  display: flex;\n  align-items: center;\n  gap: 10px;\n  padding: 5px 12px;\n  border: 1.5px solid $border-color;\n  border-radius: $border-radius;\n  background: var(--SmartThemeBlurTintColor);\n  color: $text-color;\n  cursor: pointer;\n  transition: all 0.2s ease;\n  min-height: 44px;\n  box-sizing: border-box;\n\n  &:hover {\n    background: $bg-hover;\n    border-color: $em-color;\n  }\n\n  input[type='checkbox'] {\n    display: none;\n  }\n\n  .checkbox-icon {\n    width: 18px;\n    height: 18px;\n    border: 1.5px solid $border-color;\n    border-radius: 3px;\n    background: transparent;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    flex-shrink: 0;\n    transition: all 0.2s ease;\n\n    i {\n      font-size: 11px;\n      color: rgba(100, 200, 255, 0.9);\n    }\n  }\n\n  input[type='checkbox']:checked ~ .checkbox-icon {\n    background: rgba(100, 200, 255, 0.15);\n    border-color: rgba(100, 200, 255, 0.6);\n  }\n\n  .checkbox-label {\n    font-size: 12px;\n    font-weight: 500;\n    font-family: var(--monoFontFamily, 'Consolas', monospace);\n    user-select: none;\n  }\n\n  @media (max-width: $mobile-breakpoint) {\n    padding: 10px 14px;\n    gap: 12px;\n    min-height: 40px;\n\n    .checkbox-icon {\n      width: 20px;\n      height: 20px;\n\n      i {\n        font-size: 12px;\n      }\n    }\n\n    .checkbox-label {\n      font-size: 14px;\n    }\n  }\n}\n\n.cta-group {\n  display: flex;\n  gap: 6px;\n  flex-shrink: 0;\n  align-items: center;\n\n  @media (max-width: $mobile-breakpoint) {\n    width: 100%;\n    gap: 8px;\n  }\n}\n\n.input-row--string .cta-group {\n  flex-direction: column;\n  align-self: stretch;\n  justify-content: flex-start;\n  padding-left: 10px;\n  gap: 8px;\n  flex: 0 0 96px;\n}\n\n.input-row--string .cta-group .pill {\n  width: 100%;\n}\n\n@media (max-width: $mobile-breakpoint) {\n  .input-row--string {\n    align-items: stretch;\n  }\n\n  .input-row--string .cta-group {\n    flex-direction: row;\n    flex-wrap: wrap;\n    padding-left: 0;\n    flex: 1 1 100%;\n    justify-content: flex-end;\n    gap: 10px;\n  }\n\n  .input-row--string .cta-group .pill {\n    flex: 1 1 30%;\n    min-width: 90px;\n  }\n}\n\n.pill {\n  padding: 6px 14px;\n  border: 1.5px solid $border-color;\n  border-radius: $border-radius;\n  background: transparent;\n  color: $text-color;\n  font-size: 11px;\n  cursor: pointer;\n  white-space: nowrap;\n  transition: all 0.2s ease;\n  flex-shrink: 0;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  text-align: center;\n  line-height: 1.2;\n  min-height: 24px;\n  min-width: 48px;\n\n  &:hover {\n    background: $bg-hover;\n    border-color: $em-color;\n  }\n\n  &.danger {\n    color: rgba(255, 100, 100, 0.9);\n    border-color: rgba(255, 100, 100, 0.6);\n\n    &:hover {\n      background: rgba(255, 100, 100, 0.1);\n      border-color: rgba(255, 150, 150, 0.9);\n    }\n\n    @media (max-width: $mobile-breakpoint) {\n      flex: 0.5;\n      min-width: 0;\n    }\n  }\n\n  &.ghost {\n    background: transparent;\n\n    &:hover {\n      background: $bg-hover;\n      border-color: $em-color;\n    }\n  }\n\n  &.small {\n    padding: 4px 10px;\n    font-size: 10px;\n    min-height: 22px;\n    min-width: 40px;\n\n    @media (max-width: $mobile-breakpoint) {\n      flex: 1;\n      padding: 10px 14px;\n      font-size: 13px;\n      min-height: 40px;\n      min-width: 0;\n    }\n  }\n\n  @media (max-width: $mobile-breakpoint) {\n    flex: 1;\n    padding: 10px 16px;\n    font-size: 13px;\n    min-height: 40px;\n    min-width: 0;\n  }\n}\n\n.quick-row {\n  display: flex;\n  gap: 4px;\n  padding-top: 6px;\n  border-top: 1px dashed $border-color;\n  margin-top: 2px;\n  width: 100%;\n\n  @media (max-width: $mobile-breakpoint) {\n    gap: 6px;\n    padding-top: 8px;\n    display: grid;\n    grid-template-columns: repeat(2, 1fr);\n  }\n}\n"],"sourceRoot":""}]);
